
.ratings {
  margin-top: 1px;
  background-color: #d4d6d9;
  overflow-y: auto;
  top: 170px;
  bottom: 47px;
  position: absolute;
  width: 100%;
}
.grade-box {
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
  padding-bottom: 18px;
  background-color: #fff;
}
.grade-left {
  -ms-flex: 0.4;
      flex: 0.4;
  text-align: center;
  border-right: 1px solid #d4d6d9;
}
.grade-right {
  -ms-flex: 0.6;
      flex: 0.6;
  text-align: left;
  padding-left: 24px;
}
.grade-item {
  margin-top: 8px;
}
.grade-num {
  font-size: 24px;
  color: rgb(255, 153, 0);
}
.grade-font {
  font-size: 12px;
  color: rgb(7, 17, 27);
  vertical-align: initial;
}
.star-num {
  font-size: 12px;
  color: rgb(255, 153, 0);
}
.grade-time {
  font-size: 12px;
  color: #d4d6d9;
}
.grade-gray {
  font-size: 10px;
  color: #d4d6d9;
}
.choose-box {
  background-color: #fff;
  margin-top: 20px;
  padding: 18px;
}
.choose-descript {
  border-top: 1px solid #d4d6d9;
  margin-top: 18px;
  padding-top: 18px;
}
.choose-btn {
  border: none;
  height: 36px;
  padding: 5px 15px;
  font-size: 15px;
}
.choose-btn span {
  font-size: 8px;
  margin-left: 5px;
}
.btn-blue {
  color: #fff;
  background-color: #00a0dc;
}
.btn-sky {
  color: #515a62;
  background-color: #ccecf8;
}
.btn-glay {
  color: #515a62;
  background-color: #e9ebec;
}
.choose-descript {
  color: #b7bbbf;
}
.choose-descript .icon-cancel {
  color: #fff;
  background-color: #b7bbbf;
  display: inline-block;
  width: 30px;
  height: 30px;
  text-align: center;
  line-height: 30px;
  border-radius: 100%;
}
.comment-list {
  margin-top: 1px;
  background: #fff;
  padding: 0 18px 20px 18px;
}
.comment-item {
  padding-bottom: 18px;
  padding-top: 18px;
  border-bottom: 1px solid #b7bbbf;
}
.comment-item .head-portrait {
  display: -ms-flexbox;
  display: flex;
}
.comment-item .portrait-pic {
  -ms-flex: 0.1;
      flex: 0.1;
  text-align: left;
}
.pic {
  background: url(http://static.galileo.xiaojukeji.com/static/tms/default_header.png)
    no-repeat;
  background-size: 100% 100%;
  border-radius: 100%;
  height: 28px;
  width: 28px;
  margin: 0 auto;
}
.portrait-descript {
  -ms-flex: 0.9;
      flex: 0.9;
  padding-left: 12px;
}
.comment-item .nickname {
  font-size: 10px;
  color: rgb(7, 17, 27);
  text-align: left;
}
.comment-item .portrait-time {
  font-size: 10px;
  color: rgb(147, 153, 159);
  font-weight: 200;
  text-align: left;
}
.comment-list .text {
  margin-left: 40px;
  margin-top: 6px;
  font-size: 15px;
  color: rgb(7, 17, 27);
}
.comment-list .recommend {
  margin-left: 40px;
  margin-top: 8px;
}
.comment-list .recommend .heart {
  font-size: 25px;
  color: rgb(247, 7, 7);
  vertical-align: super;
}
.comment-list .recommend .recommend-tab {
  display: inline-block;
  border: 1px solid rgba(7, 17, 27, 0.1);
  font-size: 9px;
  padding: 6px;
  color: rgb(147, 153, 159);
  width: 65px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}



