
.seller{
  background-color: #F3F5F7;
  top: 170px;
  bottom: 47px;
  position: absolute;
  width: 100%;
  overflow-y: auto;
}
.seller h2{
  font-size: 14px;
  color: rgb(7,17,27);
  margin-bottom: 8px;
  font-weight: 800;
}
.seller-info-style{
  padding: 18px 18px 0 18px;
  background-color: #fff;
}
.message .message-span{
  font-size: 10px;
  color:rgb(77,85,93);
  margin-left: 8px
}
.mind{
  text-align: center;
}
.mind-icon{
  font-size: 24px;
  color: rgb(240,20,20);
}
.mind-descript{
  margin-top: 4px;
  font-size: 10px;
  color: rgb(77,85,93);
}
.seller-message {
  display: -ms-flexbox;
  display: flex;
  margin-top: 1px;
}
.message {
  -ms-flex: .9;
      flex: .9;
}
.mind {
  -ms-flex: .1;
      flex: .1;
}
.score-box {
  -ms-flex: 1;
      flex: 1;
}
.score-box{
  border-top: 1px solid rgba(7,17,27,.1);
  margin: 18px 18px 0 18px;
  padding: 18px 0;
  border-width: 80%;
  display: -ms-flexbox;
  display: flex;
}
.score-item{
  -ms-flex: .35;
      flex: .35;
  text-align: center;
}
.data-title{
  font-size: 10px;
  color:rgb(147,153,159);
}
.seller-data{
  font-size: 24px;
  margin-top: 6px;
  font-weight: 200;
}
.small-word{
  font-size: 10px;
}
.score-border{
  border-left: 1px solid rgba(7,17,27,.1);
  border-right: 1px solid rgba(7,17,27,.1);
}
.seller-deils{
  margin-top: 20px;
  padding-bottom: 16px;
}
.seller-deils>p{
  font-size: 12px;
  font-weight: 200;
  color: rgb(240,20,20);
  line-height: 24px;
}
.type{
  background-size: 100% 100%;
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: top;
  background-position: center;
  background-repeat: no-repeat;
}
.type0{
  background-image: url("./decrease_3@2x.png");
}
.type1{
  background-image: url("./discount_3@2x.png");
}
.type2{
  background-image: url("./special_3@2x.png");
}
.type3{
  background-image: url("./invoice_3@2x.png");
}
.type4{
  background-image: url("./guarantee_3@2x.png");
}
.seller-deils-item{
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid rgba(7,17,27,.1);
  font-size: 12px;
  font-weight: 200;
  color:rgb(7,17,27);
  line-height: 16px;
}
.live-action {
 overflow: hidden; 
 margin-top: 20px;
 padding-bottom: 16px;
}
.live-imgs{
  overflow-y: hidden;
  overflow-x: auto;
  max-height: 200px;
  width: auto;
  white-space:nowrap;
}
.live-action-item:nth-child(1){
  margin-left: 0px;
}
.live-action-item{
  display: inline;
  margin-left: 6px;
  white-space: nowrap;
}
.seller-info{
  margin-top: 20px;
}
.info-item{
  padding:16px 12px;
  border-top: 1px solid rgba(7,17,27,.1) ;
}



