
.star-box {
  display: inline;
  vertical-align: sub;
}
.star:nth-child(1) {
  margin-left: 0px;
}
.star {
  margin-left: 10px;
  display: inline-block;
  width: 20px;
  height: 20px;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}
.on {
  background-image: url("./star24_on@2x.png");
}
.half {
  background-image: url("./star24_half@2x.png");
}
.off {
  background-image: url("./star24_off@2x.png");
}



